.cardList {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -8px 134px;
}

    .cardListCard {
        width: calc(100% / 5);
        padding: 15px 9px;
    }

    .cardListCard h4,
    .cardListCard p {
        text-align: initial;
    }

    .cardListCard h4  {
        font-size: 14px;
        font-weight: 600;
    }

    .cardListMessage {
        text-align: center;
        color: #fff;
        font-weight: 600;
        font-size: 28px;
        width: 100%;
    }

@media (max-width: 991px) {
    .cardListCard {
        width: calc(100% / 3);
    }
}

@media (max-width: 480px) { 
    .cardListCard {
        width: 50%;
    }
}