.SectionTitle {
    font: normal 400 75px/1 var(--font-secondary);
    text-transform: uppercase;
    color: #fff;
}

    .SectionTitle span {
        padding-left: 36px;
        display: block;
        color: var(--color-primary);
    }

@media (max-width: 1366px) {

    .SectionTitle {
        font-size: 50px;
    }

}

@media (max-width: 1199px) {

    .SectionTitle {
        font-size: 42px;
    }

}

@media (max-width: 991px) { 

    .SectionTitle span {
        display: inline-block;
        padding: 0;
    }
}